/**
* InsurerLogoCarousel.js
* Insurer Logos for PolicyPal
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
// import { Carousel } from 'react-responsive-carousel';
// import OwlCarousel from 'react-owl-carousel2';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';
import './owlcarousel.scss';

const options = {
  items: 8,
  nav: true,
  rewind: true,
  autoplay: true,
};

class InsurerLogoCarousel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: 0,
    };

    this.renderLogo = this.renderLogo.bind(this);
    this.updateScreenWidth = this.updateScreenWidth.bind(this);
  }

  componentDidMount() {
    this.updateScreenWidth();
    window.addEventListener('resize', this.updateScreenWidth);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateScreenWidth);
  }

  updateScreenWidth() {
    this.setState({ width: window.innerWidth });
  }

  renderLogo() {
    const { monotone } = this.props;
    const logosToUse = monotone ? Logos.smallTinted : Logos.small;

    return _.map(logosToUse, (logo, index) => (
      <div className="insurer-logo-sq" key={index}>
        <img src={logo} alt={`logo-${index}`} />
      </div>
    ));
  }

  render() {
    const { width } = this.state;
    let carouselOptions = { ...options, ...this.props.options };

    if (width < 360) {
      carouselOptions = { ...options, ...this.props.options, ...{ items: 3 } };
    } else if (width < 520) {
      carouselOptions = { ...options, ...this.props.options, ...{ items: 4 } };
    }

    return null;
    // return (
    //   <OwlCarousel
    //     ref="logo"
    //     options={carouselOptions}
    //   >
    //     {this.renderLogo()}
    //   </OwlCarousel>
    // );
  }
}

InsurerLogoCarousel.defaultProps = {
  monotone: false,
  options: {},
};

InsurerLogoCarousel.propTypes = {
  monotone: PropTypes.bool,
  options: PropTypes.object,
};

export default InsurerLogoCarousel;
