import _ from 'lodash';
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Carousel } from 'react-responsive-carousel';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

// Images
import Ads from 'assets/images/Landing';
import LandingPageHeader from 'assets/images/Landing/getcovered.png';

// Import button
import Button from 'components/FormsV3/Button/Button';

class LandingHeaderCarousel extends Component {
  renderInitialSlide() {
    const { history } = this.props;
    return (
      <div className="initial-splash" key={1}>
        <LazyLoadImage
          className="get-covered"
          alt="landing-page-header.jpg"
          // height={image.height}
          src={LandingPageHeader}
          // width={image.width}
        />
        {/* <img src="https://webassets.policypal.com/LandingPromo/get-covered.png" className="get-covered" alt="landing-page-header.jpg" /> */}
        <div className="desc">
          {/* <p className="landing-font d-md-block d-lg-none d-xl-none">
            Insurance<br /> just got easier
          </p>
          <p className="landing-font d-sm-none d-none d-md-none d-lg-block d-xl-block">
            Insurance just got easier
          </p> */}

          <div className="start-btn">
            <Button
              variant="medium-color"
              width={225}
              height={60}
              onClick={() => history.push('/categories')}
            >
              <p>Browse all categories</p>
            </Button>
          </div>
        </div>
      </div>
    );
  }
  renderSlides() {
    const { history } = this.props;
    const adItems = Ads.ads;
    const slides = _.map(adItems, (ad, index) => {
      if (ad.containerClass === 'cashback') {
        return (
          <div key={`landing${index}`} className={`${ad.containerClass} landing-slides`}>
            <Row className="landing-slides-inner">
              <Col
                className="d-none d-md-block"
                md={{ size: 6 }}
              />
              <Col
                className="landing-circle"
                sm={{ size: 8 }}
                md={{ size: 6 }}
              >
                {
                  ad.srcAnchor ?
                    <a className={ad.srcClass} href={ad.srcAnchor} target="__blank">
                      {/* <img src={ad.src} alt={ad.altText} /> */}
                      <LazyLoadImage
                        alt={ad.altText}
                        // height={image.height}
                        src={ad.src}
                        // width={image.width}
                      />
                    </a>
                  :
                    // <img className={ad.srcClass} src={ad.src} alt={ad.altText} />
                    <LazyLoadImage
                      className={ad.srcClass}
                      alt={ad.altText}
                      // height={image.height}
                      src={ad.src}
                      // width={image.width}
                    />
                }
              </Col>
              <Col
                className="landing-des"
                xs={{ size: 12 }}
                md={{ size: 10, offset: 1 }}
              >
                {_.map(ad.title, (text, idx) => <p key={`${ad.titleClass}-title-${idx}`} className={`title ${ad.titleClass}`}>{text}</p>)}
                <p className="description">{ad.description}</p>
                <Row className="button-container" noGutters>
                  <Col className="learn-more" xs={12} sm={6}>
                    <Button
                      variant="medium-outline-inverse"
                      width={165}
                      height={60}
                      onClick={() => history.push('/cashback')}
                    >
                      <p>Learn more</p>
                    </Button>
                  </Col>
                  <Col className="buy-policy" xs={12} sm={6}>
                    <Button
                      variant="medium-color"
                      width={165}
                      height={60}
                      onClick={() => { if (ad.route) history.push(ad.route); }}
                    >
                      <p>{ad.button}</p>
                    </Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </div>
        );
      }

      return (
        <div key={`landing${index}`} className="landing-slides">
          <Row className={`${ad.containerClass} landing-slides-inner`}>
            <Col
              className="landing-des"
              xs={{ size: 10 }}
              md={{ size: 6 }}
            >
              <p className={`title ${ad.titleClass}`}>{ad.title}</p>
              <p>{ad.description}</p>
              <div className="button-container">
                {
                ad.anchor ?
                  <a href={ad.anchor} target="__blank">
                    <Button
                      variant="medium-color"
                      width={165}
                      height={60}
                    >
                      <p>{ad.button}</p>
                    </Button>
                  </a>
                :
                  <Button
                    variant="medium-color"
                    width={165}
                    height={60}
                    onClick={() => { if (ad.route) history.push(ad.route); }}
                  >
                    <p>{ad.button}</p>
                  </Button>
                }
              </div>
            </Col>
            <Col
              className="landing-circle"
              sm={{ size: 8 }}
              md={{ size: 6 }}
            >
              {
                ad.srcAnchor ?
                  <a className={ad.srcClass} href={ad.srcAnchor} target="__blank">
                    <img src={ad.src} alt={ad.altText} />
                    <LazyLoadImage
                      alt={ad.altText}
                      // height={image.height}
                      src={ad.src}
                      // width={image.width}
                    />
                  </a>
                :
                  // <img className={ad.srcClass} src={ad.src} alt={ad.altText} />
                  <LazyLoadImage
                    className={ad.srcClass}
                    alt={ad.altText}
                    // height={image.height}
                    src={ad.src}
                    // width={image.width}
                  />
              }
            </Col>
          </Row>
        </div>
      );
    });
    const initialElement = this.renderInitialSlide();
    // slides.unshift(initialElement);
    // console.log(slides);
    // slides.splice(1, 0, initialElement);
    // console.log(slides);
    return [...slides, initialElement];
  }

  render() {
    return (
      <Carousel
        selectedItem={0}
        showThumbs={false}
        showIndicators={true}
        showArrows={false}
        showStatus={false}
        swipeable={true}
        emulateTouch={true}
        infiniteLoop={true}
        interval={4000}
        autoPlay={false}
        swipeScrollTolerance={100}
        className="landing-carousel"
      >
        {this.renderSlides()}
      </Carousel>
    );
  }
}

LandingHeaderCarousel.defaultProps = {
};

LandingHeaderCarousel.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(LandingHeaderCarousel);
