import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';

// import SVG
import TravelSVG from 'assets/images/Categories/GreenNew/Travel.svg';
import MotorSVG from 'assets/images/Categories/GreenNew/Motor.svg';
import PetSVG from 'assets/images/Categories/GreenNew/Pet.svg';
import PaSVG from 'assets/images/Categories/GreenNew/PA.svg';
// import MoreSVG from 'assets/images/Categories/More.svg';

const icons = [
  {
    src: TravelSVG,
    alt: 'travel-category-btn.jpg',
    route: '/travel',
    url: '/travel',
  },
  {
    src: MotorSVG,
    alt: 'motor-category-btn.jpg',
    route: '/motor',
    url: '/motor',
  },
  {
    src: PetSVG,
    alt: 'pet-category-btn.jpg',
    route: '/pet',
    url: '/pet',
  },
  {
    src: PaSVG,
    alt: 'personal-accident-category-btn.jpg',
    route: '/personal-accident',
    url: '/personal-accident',
  },
  // {
  //   src: MoreSVG,
  //   alt: 'more-category-btn.jpg',
  //   route: '/categories',
  //   URL: '/categories',
  // },
];

class CategoryIcons extends Component {
  renderButton(icon) {
    const { history } = this.props;

    return (
      <button
        key={icon.route}
        className="cat-img-button"
        onClick={() => history.push(icon.route)}
      >
        <img src={icon.src} className="cat-img" alt={icon.alt} />
      </button>
    );
  }

  renderIcons() {
    const result = [];
    for (let i = 0; i < icons.length; i += 2) {
      const newCol = (
        <Col xs={12} className="cat-img-container" key={i}>
          {this.renderButton(icons[i])}
          {this.renderButton(icons[i + 1])}
        </Col>
      );

      result.push(newCol);
    }

    return result;
  }

  render() {
    const { history } = this.props;

    return (
      <Row noGutters>
        {this.renderIcons(icons)}
        <Col className="cat-icon-more-button" xs={12}>
          <Button
            variant="medium-color"
            width={270}
            height={60}
            onClick={() => history.push('/categories')}
          >
            <div className="plus fa fa-plus" /><span> More Categories</span>
          </Button>
        </Col>
      </Row>
    );
  }
}

CategoryIcons.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(CategoryIcons);
